
#resumePreview {
  background: #fff;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.25);
  padding: 2em;
  color: #333;
  line-height: 1.2;
  overflow: auto;
}
#resumePreview * {
  box-sizing: border-box;
  font-variant: normal;
  font-weight: normal;
}
#resumePreview ol {
  list-style: none;
}
#resumePreview section+section {
  margin-top: 2em;
}
#resumePreview p {
  white-space: pre-line;
}
#resumePreview h2:first-child {
  background: #ddd;
  display: inline-block;
  padding: 0.2em;
  margin-bottom: 0.5em;
}
section[data-name="profile"]>h1 {
  margin: 0.1em 0;
  font-size: 4em;
}
section[data-name="work"] li+li,
section[data-name="projects"] li+li,
section[data-name="awards"] li+li {
  margin-top: 1em;
}
section[data-name="work"] li h3,
section[data-name="projects"] li h3,
section[data-name="awards"] li h3 {
  border-bottom: 1px solid #999;
  padding-bottom: 0.3em;
  margin-bottom: 0.3em;
}
section[data-name="education"] li {
  line-height: 1.5;
}
section[data-name="education"] li h3 {
  border-bottom: 1px solid #999;
  padding-bottom: 0.3em;
  margin-bottom: 0.3em;
}
section[data-name="contacts"] td:first-child {
  padding-right: 1em;
}


